import { Image } from "react-bootstrap";
import Button from "react-bootstrap/Button";
import Container from "react-bootstrap/Container";
import Form from "react-bootstrap/Form";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";

import { Link, NavLink } from "react-router-dom";

function Navigation() {
	return (
		<>
			<Container className="bg-dark  ">
				<Navbar
					bg="light"
					expand="lg"
					fixed="top"
					className="rounded mt-5 border "
					id="navbar"
				>
					<Container>
						<Navbar.Brand>
							<Link to="/Clone/">
								<Image
									src={"./assets/tree-svgrepo-com.svg"}
									width="30px"
									height="30px"
									alt="tree"
									style={{ marginRight: "1rem" }}
								/>
								Linktree
							</Link>
						</Navbar.Brand>
						<Navbar.Toggle aria-controls="navbarScroll" />
						<Navbar.Collapse id="navbarScroll">
							<Nav className="me-auto my-2 my-lg-0 p-1 m-1" navbarScroll>
								<Nav.Item>
									<Link to="/marketplace">Marketplace</Link>
								</Nav.Item>
								<Nav.Item>
									<Link to="/discover">Discover</Link>
								</Nav.Item>
								<Nav.Item>
									<Link to="/pricing">Pricing</Link>
								</Nav.Item>
								<Nav.Item>
									<Link to="/learn">Learn</Link>
								</Nav.Item>
								<NavLink to="/login" className="bg-primary text-dark">
									Login
								</NavLink>
								<Nav.Item className="bg-primary text-dark">
									<Link to="/learn">Learning</Link>
								</Nav.Item>
							</Nav>

							<Form className="d-flex ">
								<Button
									className="btn btn-secondary text-dark rounded  "
									style={{ marginRight: "1rem" }}
								>
									<Link to="/login">Log In</Link>
								</Button>
								<Button className="btn btn-dark text-light rounded ">
									<Link to="/login">Sign Up Free</Link>
								</Button>
							</Form>
						</Navbar.Collapse>
					</Container>
				</Navbar>
			</Container>
		</>
	);
}
export default Navigation;
